import React from "react";
import HeaderBox from "../component/HeaderBox";
import MainBodyBox from "../component/MainBodyBox";
import { styled } from "@mui/material/styles";
import Box from "@mui/material/Box";
import Paper from "@mui/material/Paper";
import Grid from "@mui/material/Grid";
import { mainInfo } from "../redux/reducers/question";
import { useDispatch, useSelector } from "react-redux";
import Container from "@mui/material/Container";
const Item = styled(Container)(({ theme }) => ({
  backgroundColor: "#f6edff",
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: "center",
  color: theme.palette.text.secondary,
}));

const mainbodylist = [
  {
    title: "Real-Time Collaboration",
    mention: "    ",
    img: "/img/main1.png",
  },
  {
    title: "Check Your Error",
    mention:
      " , ,   ,  ",
    img: "/img/main2.png",
  },
  {
    title: "Make Your Answer",
    mention:
      "Make Your Answer,1:1  ,1:1  ,1:1  ,  ",
    img: "/img/main3.png",
  },
];

const Main = () => {
  return (
    <Box sx={{ flexGrow: 1,backgroundColor: "#f6edff" }}>
      <Grid container spacing={3}  >
        <Grid item xs={4}>
          <Item  sx={{ flexGrow: 1,backgroundColor: "#f6edff"}}>
            <HeaderBox  icon="faUser" title="" number="3435" />
          </Item>
        </Grid>
        <Grid item xs={4}>
          <Item>
            <HeaderBox
              icon="faGraduationCap"
              title=""
              number="213"
            />
          </Item>
        </Grid>
        <Grid item xs={4}>
          <Item>
            <HeaderBox icon="ff" title="" number="434512" />
          </Item>
        </Grid>
        <Grid item xs={12}>
          <Item>
            {" "}
            {mainbodylist.map((item) => (
              <MainBodyBox
                title={item.title}
                mention={item.mention}
                img={item.img}
              />
            ))}
          </Item>
        </Grid>
      </Grid>
    </Box>
  );
};

export default Main;
